import { makeStyles, Theme } from "@material-ui/core";
import {Button} from '@material-ui/core'

const useStyles = makeStyles((theme: Theme) => ({
  main: {
    border:'2px solid black',
    padding:'2rem 4rem',
    marginTop:theme.spacing(1),
    borderRadius:5,
    display:'flex',
    justifyContent:'space-between'
  }
  
}));
interface props{
    data:any,
    onDelete:Function
}
const Item=({data,onDelete}:props)=>{
    const style=useStyles()
    const {name,price,quantity,sold,category,SKU}=data
    
    return <div className={`${style.main} appear-animated`} style={{width:'100%'}}>
       <div>
       <h3><strong>SKU: {SKU}</strong>-{name}</h3>
       <h4>sold: {sold} , {quantity} left</h4>

       </div>
       <div style={{width:90,display:'flex',flexDirection:'column'}} >
           <Button  color="primary" variant="contained" style={{marginBottom:4}}  >update</Button>
           <Button onClick={()=>{onDelete(SKU)}} color="secondary" variant="contained">delete</Button>
       </div>
    </div>
}



export default Item